import React from 'react'

export default function VideoBackground() {
  return (
    <React.Fragment>
      <video autoPlay muted loop style={videoBackground}>
        <source src="./Videos/game-background.mp4" type="video/mp4" />
      </video>
    </React.Fragment>
  )
}

const videoBackground = {
  position: 'fixed',
  left: 0,
  top: 0,
  minWidth: '100%',
  minHeight: '100%',
  zIndex: -1
}
