import React, { Component } from 'react';
import AvgLeaderboardEntry from './LeaderboardEntry';
import LeaderboardTitle from './LeaderboardTitle';

class AvgLeaderboard extends Component {
  avgComponents() {
    let avgComponent = this.props.avgLeaderboard.map((player, index) =>
      <AvgLeaderboardEntry key={index} position={index + 1} name={player[0]} time={player[1]} isFloat={true}/>
    );

    return avgComponent;
  }

  render() {
    return (
      <div style={Leaderboard} className='d-flex flex-column align-items-center'>
        <LeaderboardTitle text='Hall of Fame' />
        <ul style={list}>
          {this.avgComponents()}
        </ul>
      </div>
    )
  }
}

const Leaderboard = {
  backgroundColor: 'rgba(15,50,85,0.7)',
  flex: 5,
  minHeight: '500px',
  maxHeight: '1200px',
  overflowY: 'scroll',
  margin: '0 10px',
  borderRadius: '5px'
}



const list = {
  width: '70%',
  margin: '30px 0'
}

export default AvgLeaderboard
