import React, { Component } from 'react';
import LeaderboardTitle from './LeaderboardTitle';
import LevelLeaderboardCard from './LevelLeaderboardCard';
import LeaderboardEntry from './LeaderboardEntry';

export class LevelLeaderboards extends Component {
  state = {
    showLevelLeaderboard: null,
    levelLeaderboardsArray: []
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.levelLeaderboards !== this.state.levelLeaderboardsArray) {

      let lvlLeaderboardsArr = [];
      for (let player in nextProps.levelLeaderboards) {
        lvlLeaderboardsArr.push(nextProps.levelLeaderboards[player])
      }

      this.setState(prevState => {
        return {
          ...prevState,
          levelLeaderboardsArray: lvlLeaderboardsArr
        }
      })
    }
  }

  handleClick = (level) => {
    this.setState({ showLevelLeaderboard: level })
  }

  genLevelLeaderboard = (level) => {
    let lvlLeaderboardArr = [];
    for (let player in this.state.levelLeaderboardsArray[level]) {
      lvlLeaderboardArr.push([player, this.state.levelLeaderboardsArray[level][player]])
    }

    let lvlLeaderboardListItems = lvlLeaderboardArr.map((player, index) =>
      <LeaderboardEntry key={index} position={index + 1} name={player[0]} time={player[1]} isFloat={false}/>
    )

    let lvlLeaderboardComponent =
      <div className='d-flex w-100' >
        <div style={{marginLeft: '20px', flex: 1}} className='d-flex flex-column align-items-center'>
          <p style={levelNo}>Level {level+1}</p>
          <button className="backButton" onClick={()=>this.handleClick(null)}>Back</button>
        </div>
        <div style={levelLeaderboard} className='d-flex justify-content-center'>
          <ul style={list}>
            {lvlLeaderboardListItems}
          </ul>
        </div>
      </div>

    return lvlLeaderboardComponent;
  }

  genLevelComponents = () => {
    let levels = Object.keys(this.props.levelLeaderboards);

    let levelComponents = levels.map((level, index) =>
      <LevelLeaderboardCard key={index} index={index} handleClick={this.handleClick} levelNumber={level-1} />
    );
    return <div className="d-flex w-100 justify-content-around flex-wrap" style={{ padding: '10px' }}>
      {levelComponents}
    </div>;
  }

  render() {
    return (
      <div style={Leaderboard} className='d-flex flex-column align-items-center'>
        <LeaderboardTitle text='Levels Leaderboard' />
        {this.state.showLevelLeaderboard !== null ? this.genLevelLeaderboard(this.state.showLevelLeaderboard) : this.genLevelComponents()}
      </div>
    )
  }
}

const Leaderboard = {
  backgroundColor: 'rgba(15,50,85,0.7)',
  flex: 7,
  minHeight: '500px',
  maxHeight: '1200px',
  overflowY: 'scroll',
  margin: '0 10px',
  borderRadius: '5px'
}

const list = {
  width: '70%',
  margin: '30px 0'
}

const levelNo ={
  fontFamily: 'Rubik',
  color: '#aaa',
  margin: '5px 0',
  fontSize: '35px'
}

const levelLeaderboard = {
  flex: 6
}



export default LevelLeaderboards
