import React, { Component } from 'react';

export class Header extends Component {
  render() {
    return (
      <header className="d-flex flex-column justify-content-around align-items-center" style={titleContainer}>
        <div>
          <span style={logo}>TAN</span>
          <span style={title}>GRAM</span>
        </div>
        <div>
          <span style={subtitle}>Leaderboards</span>
        </div>
      </header>
    )
  }
}

const titleContainer = {
  height: '150px',
  userSelect: 'none'
}

const title = {
  color: '#fff',
  fontFamily: "Work Sans",
  fontWeight: '600',
  fontSize: '35px',
  textShadow: '0 0 3px #666'
}

const logo = {
  color: '#fff',
  fontFamily: "Work Sans",
  fontWeight: '600',
  fontSize: '35px',
  textShadow: '0 0 3px #666',
  borderRadius: '50%',
  padding: '18px 0 18px 10px',
  backgroundColor: '#CAD400'
}

const subtitle = {
  color: '#fff',
  fontFamily: "Work Sans",
  fontSize: '25px',
  textShadow: '0 0 3px #666',
}

export default Header;
