import React from 'react';

function LeaderboardEntry(props) {

  // format the timeScore
  function timerModification(time) {
    if (time < 60 && props.isFloat) {
      if (time < 10) {
        return `0:0${parseFloat(time).toFixed(2)}`
      } else {
        return `0:${parseFloat(time).toFixed(2)}`
      }
    } else if (time < 60 && !props.isFloat) {
      if (time < 10) {
        return `0:0${time}`
      } else {
        return `0:${time}`
      }
    } else if (time > 60 && props.isFloat) {
      if (time % 60 < 10) {
        return `${parseInt(time / 60)}:0${parseFloat(time % 60).toFixed(2)}`
      } else {
        return `${parseInt(time / 60)}:${parseFloat(time % 60).toFixed(2)}`
      }
    } else {
      if (time % 60 < 10) {
        return `${parseInt(time / 60)}:0${parseInt(time % 60)}`
      } else {
        return `${parseInt(time / 60)}:${parseInt(time % 60)}`
      }
    }
  }

  //choose correct css based on players position
  function listItemStyle(index) {
    if (index === 1) {
      return {
        fontSize: '45px',
        ...listItem
      }
    } else if (index === 2) {
      return {
        fontSize: '30px',
        ...listItem
      }
    } else if (index === 3) {
      return {
        fontSize: '25px',
        ...listItem
      }
    } else {
      return {
        fontSize: '18px',
        ...listItem
      }
    }
  }

  return (
    <li style={listItemStyle(props.position)} className="d-flex justify-content-between">
      <p style={positionStyle}>{props.position}</p>
      <p style={nameStyle}>{props.name}</p>
      <p style={timeStyle}>{timerModification(props.time)}</p>
    </li>
  )
}

const listItem = {
  fontFamily: 'Rubik',
  color: '#aaa',
  margin: '5px 0'
};

const positionStyle = {
  width: '50px',
  textAlign: 'center'
};

const nameStyle = {
  width: '200px',
  textAlign: 'center'
};

const timeStyle = {
  width: '160px',
  textAlign: 'center'
};

export default LeaderboardEntry;