import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faStar } from '@fortawesome/free-solid-svg-icons';

 function LeaderboardTitle(props) {
  return (
    <div className='d-flex'>
      <FontAwesomeIcon icon={faStar} style={starLogo} />
      <p style={title}>{props.text}</p>
    </div>
  )
}

const starLogo = {
  height: '50px',
  fontSize: '43px',
  padding: '8px',
  position: 'relative',
  top: '5px',
  backgroundColor: '#CAD400',
  borderRadius: '50%',
  color: '#fff'
}

const title = {
  fontFamily: 'Rubik',
  fontSize: '3rem',
  color: '#fff'
}

export default LeaderboardTitle