import React, { Component } from 'react'
import AvgLeaderboard from './AvgLeaderboard'
import LevelLeaderboards from './LevelLeaderboards'

export class MainContent extends Component {
  state = {
    levelLeaderboards: [],
    avgLeaderboard: []
  }

  async componentDidMount() {
    console.log("fetching data...");

    let levelLeaderboards = {};
    let avgLeaderboard = [];

    await fetch('../../api/leaderboard')
      .then(response => response.json())
      .then(data => levelLeaderboards = data)

    await fetch('../../api/avgLeaderboard')
      .then(response => response.json())
      .then(data => avgLeaderboard = data)


    this.setState({
      levelLeaderboards: levelLeaderboards,
      avgLeaderboard: avgLeaderboard,
    })

  }

  render() {
    return (
      <div className='d-flex'>
        <AvgLeaderboard avgLeaderboard={this.state.avgLeaderboard} />
        <LevelLeaderboards levelLeaderboards={this.state.levelLeaderboards} />
      </div>
    )
  }
}

export default MainContent
