import React, { Component } from 'react'

class LevelLeaderboardCard extends Component {
  render() {
    return (
      <React.Fragment>
        <div style={levelCard} onClick={() => this.props.handleClick(this.props.index)} className='levelCard d-flex justify-content-center align-items-center'>
          <p style={levelNumber} className="levelNumber">{this.props.levelNumber}</p>
        </div>
        
      </React.Fragment>
    )
  }
}

const levelCard = {
  width: '19%',
  height: '100px',
  margin: '5px 0',
  transitionDuration: '.4s'
}

const levelNumber = {
  fontSize: '35px',
}

export default LevelLeaderboardCard
